.Added {
    color: green;
    background-color: #D5F5E3;
    text-align: center;
    font-weight: bold;
    margin-left: 40px;
    margin-bottom: 10px;
    padding-top: 5px; 
    width: 200px;
    height: 25px;
    border: 1px solid #79706E;
    border-radius: 5px;
}

.Error {
    color: red;
    background-color: #FADBD8 ;
    text-align: center;
    font-weight: bold;
    margin-left: 40px;
    margin-bottom: 10px;
    padding-top: 5px; 
    border: 1px solid #79706E;
    border-radius: 5px;
    width: max-content;
}

.Input {
    margin-left: 15px;
}